import { LlmAuditTraceMessage, LlmCqsTrace } from "@rp/react/tanstack-api/llm-summary"

const getTimestampFromAudit = (
  documentId: string,
  audit: Array<Omit<LlmAuditTraceMessage["trace"], "queryId">>,
) => {
  const relatedStory = audit
    .flatMap(question => question.results)
    .filter((result): result is LlmCqsTrace => result.type === "CQS")
    .flatMap(result => result.values)
    .filter(story => story.id === documentId)[0]

  return relatedStory?.ts
}

export { getTimestampFromAudit }
